
import XInput from 'vux/src/components/x-input/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'

import {saveSearch} from '../../../static/js/cache.js'  
import storage from 'good-storage'  
var searchData = []
export default({
directives: {
TransferDom
},
data () {
return {
searchVal:'',
searchList: storage.get('_search_') || [],
IsShow:false
}
},
components: {
XInput,
XButton,
Icon,
Confirm
},
methods: {
onCancel(){
this.IsShow = false
},
onConfirm(){
storage.remove('_search_');
this.searchList = storage.get('_search_') || []
},
onShow(){
this.IsShow = true
},
onHide(){
this.IsShow = false
},
toList(e){
let that = this
let searchVal = that.searchVal
let searches=storage.get('_search_') || [];
if( that.searchVal.match(/^\s*$/) == null){
if(searches.indexOf(searchVal) === -1){
saveSearch(searchVal)
}
}
that.$router.push({path:`/searchlist/${searchVal}`})
},
clearStorage(e){
this.IsShow = true
}
},
mounted () {

}
})



